.lifter-list {
	width: 60%;
	margin: 20px auto;	
}

.lifter-list .title {
	padding: 0px 0px 5px 6px;
	font-weight: bold;
	font-size: 1.2em;
	text-transform: uppercase;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: center;
	    align-items: center;
	border-bottom: 1px solid black;
	/*margin-bottom: 12px;*/
}

.lifter-list .aside {
	font-weight: normal;
	font-size: 0.8em;
	text-transform: none;
	/*font-style: italic;*/
	margin-left: 20px;
	color: #999;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	/*display: inline-block;*/
}

.lifter-list .item {
	height: 40px;
	padding: 3px 6px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: center;
	    align-items: center;
	border-bottom: 1px dashed #dadada;
}

/*.lifter-list .item .weightClass {
    font-size: 0.75em;
    color: #999;
}*/
.lifter-list .item .yob {
    margin-right: 40px;
}

.lifter-list .item:hover{
	color: unset;
}

.lifter-list .item:hover {
  background-color: #efefef;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  color: black;
}


