.searchbox input {
    width: 280px;
    border: none;
    outline: 0px;
    outline-offset: 0px;
    height: 26px;
    line-height: 26px;
    font-size: 16px;
    padding: 1px 6px;
    -webkit-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s;
}

.searchbox input:focus {
  width: 280px;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
  outline-offset: 0px;
}

.searchbox input::-webkit-input-placeholder {
  font-size: 14px;
  color: #ccc;
}

.searchbox input:-ms-input-placeholder {
  font-size: 14px;
  color: #ccc;
}

.searchbox input::placeholder {
  font-size: 14px;
  color: #ccc;
}



